/**
 * preview panel styles
 */
[data-tc="preview-panel"] {
    width: 100%;
    min-height: var(--tcfu-min-height);
    display: flex;
    flex-direction: column;
}

[data-tc="buttons"] {
    margin-top: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

[data-tc="preview"] {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
}

.tcfu-file-box{
    position: relative;
}

.tcfu-remove-btn {
    position: absolute;
    appearance: none;
    cursor: pointer;
    left: 3px;
    top: 3px;
    border: 0;
    border-radius: 3px;
    background: rgb(17 25 39);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
}

embed {
    max-width: 100%;
    border: 0;
}